.about_container{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  position: relative;

  @media screen and (max-width:700px) {
    grid-template-columns: 1fr;
  }
  
  .about_left{
     background: #573e37;
     max-width: 350px;
     height: 350px;
     justify-self: center;
     border-radius: 5px;
     box-shadow: 0 0 20px #573e37;

     img{
      width: 100%;
      height: 100%;
      object-fit: cover;
     }
  }
  .about_right{
  color: #573e37;
  padding: 1rem;
  display: flex;
  flex-direction: column;

  @media screen and(max-width: 700px) {
    padding: 0;
    margin-top: 2rem;
  }

  p {
    font-size: 0.85rem;
    margin-bottom: 1rem;
    max-width: 450px;
  }
  .bio{
    display: flex;
    font-size: 0.85rem;
    min-width: 120px;
    margin-bottom: 1rem;

    .biokey{
      display: flex;
      align-items: center;
      gap: 0.5rem;
      margin-right: 3rem;
    }

  
    }
    a{
      text-decoration: none;
      background: #2e3e72;
      color: #fff;
      text-align: center;
      font-size: 0.85rem;
      text-transform: capitalize;
      width: 166px;
      border-radius: 10px;
      padding: 0.65rem;

    }
  }

}


